.page {
  &:has(.page__menu:target) {
    overflow: hidden;
  }

  font-family: Manrope, sans-serif;
  color: $main-text-color;
  font-size: 16px;
  line-height: 140%;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
    transform: translateX(-100%);
    transition: all 0.3s;
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

.main {
  background-color: #fff;
  &__content {
    display: grid;
    row-gap: 64px;
    padding-block: 64px;

    @include onTablet {
      row-gap: 120px;
      padding-block: 120px;
    }
  }
}
